<template>
  <div class="realname">
    <div class="checkplan">
      <el-tabs v-model="activeName" @tab-click="handleClick" :stretch="true" class="checkplan-bg">
          <!-- 进行中计划 -->
          <el-tab-pane label="进行中计划" name="first" class="infinite-list-wrapper" style="overflow:auto">
            <div class="flex-height" v-infinite-scroll="load" infinite-scroll-disabled="disabled" infinite-scroll-immediate="false">
              <div class="checklist" @click="toparticular(item)" v-for="(item, i) in checkplanList" :key="i">
                <div class="checklist-top">
                  <div class="head-title">
                    <span>{{item.bank_name}}</span>
                    <span class="mar-left">尾号&nbsp;{{ item.BANK_ACCOUNT !== null ? (item.BANK_ACCOUNT.substring(item.BANK_ACCOUNT.length-4, item.BANK_ACCOUNT.length)) : '' }}</span>
                    <span class="mar-right">{{item.MERCHANT_CN_NAME}}</span>
                  </div>
                  <div class="center-title">
                    <span>账单日：{{item.BILL_DAY}}</span>
                    <span>还款日：{{item.REPAYMENT_DAY}}</span>
                    <span>状态：{{item.STATUS !== '10B' ? (item.STATUS !== '10A' ? '暂停' : '未执行' ) : '进行中' }}</span>
                  </div>
                  <div class="bottom-title">
                    <span>开始时间：{{item.START_TIME.time | formatDate}}</span>
                    <span>结束时间：{{item.END_TIME.time | formatDate}}</span>
                  </div>
                </div>
                <div class="checklist-bottom">
                  <div class="price-bottom">
                    <div class="price-t">还款金额</div>
                    <div class="price-c">{{item.PLAN_AMT}}</div>
                  </div>
                  <div class="price-bottom">
                    <div class="price-t">手续费</div>
                    <div class="price-c">{{item.SALE_FREE + item.PAY_FREE}}</div>
                  </div>
                </div>
              </div>
              <p class="loading" v-if="loading">加载中...</p>
              <p class="loading" v-if="noMore">没有更多了</p>
            </div>
          </el-tab-pane>
          <!-- 未执行计划 -->
          <el-tab-pane label="未执行计划" name="second" class="infinite-list-wrapper" style="overflow:auto">
            <div class="flex-height" v-infinite-scroll="load" infinite-scroll-disabled="disabled" infinite-scroll-immediate="false">
              <div class="checklist" @click="toparticular(item)" v-for="(item, i) in checkplanList" :key="i">
                <div class="checklist-top">
                  <div class="head-title">
                    <span>{{item.bank_name}}</span>
                    <span class="mar-left">尾号&nbsp;{{ item.BANK_ACCOUNT !== null ? (item.BANK_ACCOUNT.substring(item.BANK_ACCOUNT.length-4, item.BANK_ACCOUNT.length)) : '' }}</span>
                    <span class="mar-right">{{item.MERCHANT_CN_NAME}}</span>
                  </div>
                  <div class="center-title">
                    <span>账单日：{{item.BILL_DAY}}</span>
                    <span>还款日：{{item.REPAYMENT_DAY}}</span>
                    <span>状态：{{item.STATUS !== '10B' ? (item.STATUS !== '10A' ? '暂停' : '未执行' ) : '进行中' }}</span>
                  </div>
                  <div class="bottom-title">
                    <span>开始时间：{{item.START_TIME.time | formatDate}}</span>
                    <span>结束时间：{{item.END_TIME.time | formatDate}}</span>
                  </div>
                </div>
                <div class="checklist-bottom">
                  <div class="price-bottom">
                    <div class="price-t">还款金额</div>
                    <div class="price-c">{{item.PLAN_AMT}}</div>
                  </div>
                  <div class="price-bottom">
                    <div class="price-t">手续费</div>
                    <div class="price-c">{{item.SALE_FREE + item.PAY_FREE}}</div>
                  </div>
                </div>
              </div>
              <p class="loading" v-if="loading">加载中...</p>
              <p class="loading" v-if="noMore">没有更多了</p>
            </div>
          </el-tab-pane>
          <!-- 暂停计划 -->
          <el-tab-pane label="暂停计划" name="third" class="infinite-list-wrapper" style="overflow:auto">
            <div class="flex-height" v-infinite-scroll="load" infinite-scroll-disabled="disabled" infinite-scroll-immediate="false">
              <div class="checklist" @click="toparticular(item)" v-for="(item, i) in checkplanListdata" :key="i">
                <div class="checklist-top">
                  <div class="head-title">
                    <span>{{item.bank_name}}</span>
                    <span class="mar-left">尾号&nbsp;{{ item.BANK_ACCOUNT !== null ? (item.BANK_ACCOUNT.substring(item.BANK_ACCOUNT.length-4, item.BANK_ACCOUNT.length)) : '' }}</span>
                    <span class="mar-right">{{item.MERCHANT_CN_NAME}}</span>
                  </div>
                  <div class="center-title">
                    <span>账单日：{{item.BILL_DAY}}</span>
                    <span>还款日：{{item.REPAYMENT_DAY}}</span>
                    <span>状态：{{item.STATUS !== '10B' ? (item.STATUS !== '10A' ? '暂停' : '未执行' ) : '进行中' }}</span>
                  </div>
                  <div class="bottom-title">
                    <span>开始时间：{{item.START_TIME.time | formatDate}}</span>
                    <span>结束时间：{{item.END_TIME.time | formatDate}}</span>
                  </div>
                </div>
                <div class="checklist-bottom">
                  <div class="price-bottom">
                    <div class="price-t">还款金额</div>
                    <div class="price-c">{{item.PLAN_AMT}}</div>
                  </div>
                  <div class="price-bottom">
                    <div class="price-t">手续费</div>
                    <div class="price-c">{{item.SALE_FREE + item.PAY_FREE}}</div>
                  </div>
                </div>
              </div>
              <p class="loading" v-if="loading">加载中...</p>
              <p class="loading" v-if="noMore">没有更多了</p>
            </div>
          </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      input: '',
      checkplanList: [], // 定义一个空数组接收计划数据列表vm.searchTime = vm.$moment(n).format('YYYY-MM-DD')
      plantype: '10B', // 初始值为进行中计划
      activeName: 'first',
      serviceprice: '', // 手续费 = 费率 + 手续费   SALE_FREE + PAY_FREE
      cardLista: '',
      usemerchantNo: '', // 获得对应用户的mechantNo
      yema: 0,
      yeshu: 10,
      checkplanListdata: [], // 这是上拉加载的数据
      loading: false
    }
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    this.reallist()
    console.log(JSON.parse(this.$stact.state.token)[0])
  },
  computed: {
    noMore () {
      return this.checkplanList.length < this.yeshu
    },
    disabled () {
      return this.loading || this.noMore
    }
  },
  methods: {
    load () {
      this.loading = true
      setTimeout(() => {
        this.yema += 1
        this.reallist()
        this.loading = false
      }, 2000)
    },
    handleClick (tab, event) {
      let vm = this
      console.log(tab.name)
      if (tab.name === 'first') {
        vm.yema = 0
        vm.yeshu = 10
        vm.input = ''
        vm.checkplanList = [] // 定义一个空数组接收计划数据列表vm.searchTime = vm.$moment(n).format('YYYY-MM-DD')
        vm.endtime = ''
        vm.serviceprice = '' // 手续费 = 费率 + 手续费   SALE_FREE + PAY_FREE
        vm.cardLista = ''
        vm.usemerchantNo = '' // 获得对应用户的mechantNo
        vm.yema = 0
        vm.yeshu = 10
        vm.checkplanListdata = [] // 这是上拉加载的数据
        vm.loading = false
        vm.plantype = '10B' // 计划进行中
        this.reallist()
      } else if (tab.name === 'second') {
        vm.yema = 0
        vm.yeshu = 10
        vm.input = ''
        vm.checkplanList = [] // 定义一个空数组接收计划数据列表vm.searchTime = vm.$moment(n).format('YYYY-MM-DD')
        vm.endtime = ''
        vm.serviceprice = '' // 手续费 = 费率 + 手续费   SALE_FREE + PAY_FREE
        vm.cardLista = ''
        vm.usemerchantNo = '' // 获得对应用户的mechantNo
        vm.yema = 0
        vm.yeshu = 10
        vm.checkplanListdata = [] // 这是上拉加载的数据
        vm.loading = false
        vm.plantype = '10A' // 未执行计划
        this.reallist()
      } else if (tab.name === 'third') {
        vm.yema = 0
        vm.yeshu = 10
        vm.input = ''
        vm.checkplanList = [] // 定义一个空数组接收计划数据列表vm.searchTime = vm.$moment(n).format('YYYY-MM-DD')
        vm.endtime = ''
        vm.serviceprice = '' // 手续费 = 费率 + 手续费   SALE_FREE + PAY_FREE
        vm.cardLista = ''
        vm.usemerchantNo = '' // 获得对应用户的mechantNo
        vm.yema = 0
        vm.yeshu = 10
        vm.checkplanListdata = [] // 这是上拉加载的数据
        vm.loading = false
        vm.plantype = '10C' // 暂停计划
        this.reallist()
      }
      console.log(vm.plantype)
    },
    reallist () {
      let vm = this
      vm.isloading = 0
      vm.checkplanList = [] // 每次调用要清空上一次的列表，重新渲染
      let parmas = {
        '0': '0700',
        '3': '390014',
        '5': vm.yema,
        '6': vm.yeshu,
        '42': vm.merchantNo,
        '43': vm.plantype,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            // 57域是返回的数据
            vm.checkplanList = JSON.parse(res.data[57])
            for (var i in vm.checkplanList) {
              vm.checkplanListdata.push(vm.checkplanList[i])
            }
          }
        })
    },
    toparticular (itema) {
      let vm = this
      vm.checkplanListdata = []
      // 请求一次获取对应卡片用户的数据
      console.log(itema.MERCHANT_NO)
      let parmas = {
        '0': '0700',
        '3': '190932',
        '42': itema.MERCHANT_NO,
        '43': '0',
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.cardLista = JSON.parse(res.data[57])
          }
        })
        .catch(err => {
          console.log(err)
        })
      setTimeout(() => {
        this.$router.push({ name: 'particular', query: { item: JSON.stringify(itema), cardList: JSON.stringify(vm.cardLista[0]) } })
      }, 1000)
    }
  },
  filters: {
    formatDate: function (value) {
      let date = new Date(value)
      let y = date.getFullYear()
      let MM = date.getMonth() + 1
      MM = MM < 10 ? ('0' + MM) : MM
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      let h = date.getHours()
      h = h < 10 ? ('0' + h) : h
      let m = date.getMinutes()
      m = m < 10 ? ('0' + m) : m
      let s = date.getSeconds()
      s = s < 10 ? ('0' + s) : s
      return y + '-' + MM + '-' + d
    }
  }
}
</script>
